const twitchConnected = nodecg.Replicant('twitchConnected');

function onDisconnect() {
	nodecg.sendMessage('DisconnectTwitch');
}

function onConnect() {
	console.log('idk');
}

function setTwitchStatus(isConnected) {
	if (isConnected) {
		document.getElementById('twitch-status').innerText = 'Connected';
		document.getElementById('submit-disconnect').setAttribute('disabled', 'false');
		document.getElementById('submit-connect').setAttribute('disabled', 'true');
		document.getElementById('channel-name').setAttribute('readonly', 'true');
		document.getElementById('channel-name').setAttribute('disabled', 'true');
	} else {
		document.getElementById('twitch-status').innerText = 'Disconnected';
		document.getElementById('submit-connect').setAttribute('disabled', 'false');
		document.getElementById('submit-disconnect').setAttribute('disabled', 'true');
		document.getElementById('channel-name').setAttribute('readonly', 'false');
		document.getElementById('channel-name').setAttribute('disabled', 'false');
	}
}

NodeCG.waitForReplicants(twitchConnected).then(() => {
	setTwitchStatus(twitchConnected.value === true);
	document.getElementById('twitch-form').on('submit', e => {
		if (e.target.value === 'Disconnect') {
			onDisconnect();
		} else {
			onConnect();
		}
	});
});
